.header {
  display: flex;
  flex-direction: column;
  background-image: url("../../images/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

.main-heading {
  margin-top: -5vh;
  text-align: center;
}

.header-title {
  color: aliceblue;
  font-size: 5em;
}

.header-subtitle {
  color: aliceblue;
  font-size: 2.5em;
}

.sub-info {
  margin-left: 10vw;
  margin-top: 2vh;
  font-size: 1.4em;
  color: aliceblue;
  font-weight: 200;
}

.get-started {
  text-align: center;
  margin-top: 7vh;
}

.get-started-button {
  text-decoration: none;
  color: rgb(0, 32, 95);
  font-size: 1.4em;
  font-weight: bolder;
  border: 5px solid rgb(0, 32, 95);
  padding: 15px 40px;
  border-radius: 10px;
  transition: all 0.6s ease-out;
}

.get-started-button:hover {
  background: linear-gradient(
    to right,
    rgb(0, 32, 95) 2.8%,
    rgb(132, 53, 142) 97.8%
  );
  color: aliceblue;
}
