.main-ai {
  padding: 100px;
  height: 95vh;
}

.ai-column {
  width: 50vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.image-preview {
  border: 4px dashed #000;
  height: 450px;
  width: 600px;
  display: flex;
}

.image-upload-box {
  margin-top: 15px;
}

.form-handler {
  display: flex;
  flex: 1;
}

input[type="file"] {
  width: 600px;
  max-width: 100%;
  color: #000;
  padding: 10px;
  background: #faf9f6;
  border-radius: 10px;
  border: 2px solid #000;
}

input[type="file"]::file-selector-button {
  margin-right: 20px;
  border: none;
  background: rgb(0, 32, 95);
  border: 2px solid rgb(0, 32, 95);
  padding: 10px 20px;
  border-radius: 10px;
  color: aliceblue;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

input[type="file"]::file-selector-button:hover {
  background: #2596be;
  border: 2px solid rgb(0, 32, 95);
}

.ai-label {
  font-weight: bolder;
  font-size: 1.4em;
  margin-bottom: 10px;
  border: 3px solid rgb(0, 32, 95);
  padding: 10px;
  border-radius: 10px;
  width: 600px;
  background: #2596be;
  color: aliceblue;
}

.ai-submit {
  background: rgb(0, 32, 95);
  margin-top: 30px;
  opacity: 0.5;
}

.ai-submit-true {
  background: rgb(0, 32, 95);
  margin-top: 30px;
}

.theme-button {
  padding: 10px;
  margin: 15px;
  width: 150px;
  border-radius: 10px;
  background: #faf9f6;
  font-weight: bolder;
  font-size: 1.1em;
  border: 3px solid rgb(0, 32, 95);
  color: rgb(0, 32, 95);
  transition: all 0.3s ease-out;
}

.theme-button:hover {
  color: aliceblue;
  background: #2596be;
}

.selected-theme-button {
  padding: 10px;
  margin: 15px;
  width: 150px;
  border-radius: 10px;
  background: rgb(0, 32, 95);
  font-weight: bolder;
  font-size: 1.1em;
  border: 3px solid rgb(0, 32, 95);
  color: aliceblue;
  transition: all 0.3s ease-out;
}

.theme-container {
  display: flex;
  flex-direction: row;
  margin-bottom: 40px;
}

.output {
  height: 150px;
  width: 600px;
  border: 3px solid rgb(0, 32, 95);
  border-radius: 10px;
  padding: 5px 10px;
}

.output-text {
  font-size: 1.3em;
  color: #000;
  font-weight: bold;
  text-align: center;
  font-style: italic;
}

.slider {
  -webkit-appearance: none !important;
  width: 500px;
  height: 5px;
  border-radius: 10px;
  background: rgb(0, 32, 95);
  margin-right: 30px;
  border: none;
  outline: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none !important;
  width: 25px;
  height: 25px;
  background: rgb(0, 32, 95);
  border: 2px solid rgb(0, 32, 95);
  border-radius: 50%;
  cursor: pointer;
}
input[type="range"]::-webkit-slider-thumb:hover {
  background: #2596be;
  border: 2px solid #2596be;
}

.range-input {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.slider-text-box {
  width: 50px;
  text-align: center;
}

.slider-text {
  font-weight: bolder;
  font-size: 1.3em;
}
